.App .sidebar__close-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: calc(100 * var(--vh));
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 4;
  display: none;
}
.App .main-view {
  overflow-x: hidden;
  scrollbar-width: thin;
  position: relative;
  width: 100%;
  max-width: 1955px;
  height: calc(100 * var(--vh) - 90px);
  max-height: calc(100 * var(--vh) - 90px);
}
.App .main-view::-webkit-scrollbar {
  width: 5px;
}
.App .main-view::-webkit-scrollbar-thumb {
  background: #666;
}
.App .progressBar {
  --bar-bg: var(--slider-rail-color);
  --seek-before-width: 0;
  --seek-before-color: var(--secondary-text-color);
  --seek-before-color-hover: var(--spotify-faded-color);
  --knobby: var(--primary-text-color);
  --selectedKnobby: #26c9c3;
  -webkit-appearance: none;
          appearance: none;
  background: var(--bar-bg);
  border-radius: 10px;
  position: relative;
  width: 100%;
  height: 4px;
  border-top: 6px solid #181818;
  border-top: 6px solid var(--playbar-background-color);
  border-bottom: 6px solid #181818;
  border-bottom: 6px solid var(--playbar-background-color);
  outline: none;
}
.App .progressBar::-webkit-slider-runnable-track {
  background: var(--bar-bg);
  border-radius: 10px;
  position: relative;
  width: 100%;
  height: 4px;
  outline: none;
}
.App .progressBar::-moz-range-track {
  background: var(--bar-bg);
  border-radius: 10px;
  position: relative;
  width: 100%;
  height: 4px;
  outline: none;
}
.App .progressBar::-moz-focus-outer {
  border: 0;
}
.App .progressBar::before {
  content: "";
  height: 4px;
  width: var(--seek-before-width);
  background-color: var(--seek-before-color);
  border-radius: 10px;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  left: 0;
  z-index: 3;
  cursor: pointer;
}
.App .progressBar:hover::before {
  background-color: var(--seek-before-color-hover);
}
.App .progressBar:active::before {
  background-color: var(--seek-before-color-hover);
}
.App .progressBar::-moz-range-progress {
  background-color: var(--seek-before-color);
  border-radius: 10px;
  height: 4px;
}
.App .progressBar:hover::-moz-range-progress {
  background-color: var(--seek-before-color-hover);
}
.App .progressBar:active::-moz-range-progress {
  background-color: var(--seek-before-color-hover);
}
.App .progressBar::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 12px;
  width: 12px;
  border-radius: 50%;
  border: none;
  background-color: var(--knobby);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  position: relative;
  margin: -4px 0 0 0;
  z-index: 3;
  box-sizing: border-box;
  opacity: 0;
}
.App .progressBar:active::-webkit-slider-thumb {
  opacity: 1;
}
.App .progressBar:hover::-webkit-slider-thumb {
  opacity: 1;
}
.App .progressBar::-moz-range-thumb {
  height: 12px;
  width: 12px;
  border-radius: 50%;
  border: none;
  background-color: var(--knobby);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  position: relative;
  z-index: 3;
  box-sizing: border-box;
  opacity: 0;
}
.App .progressBar:active::-moz-range-thumb {
  opacity: 1;
}
.App .progressBar:hover::-moz-range-thumb {
  opacity: 1;
}

.popup {
  z-index: 999;
  width: 100vw;
  height: calc(100 * var(--vh));
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup__container {
  padding: 50px;
  border-radius: 8px;
  min-width: 500px;
  background-color: #121212;
  background-color: var(--primary-background-color);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.5);
}
.popup__container h1 {
  color: currentColor;
  font-size: 25px;
  font-weight: 600;
  border-bottom: 1px solid currentColor;
  margin-bottom: 20px;
}
.popup__container .ant-alert-error {
  background-color: #2a1215;
  border: 1px solid #58181c;
  color: #fff;
}
.popup__container .ant-alert-message {
  color: rgba(255, 255, 255, 0.85);
}
.popup__container .ant-alert-close-icon .anticon-close {
  color: rgba(255, 255, 255, 0.45);
}
.popup__container form {
  margin-top: 30px;
}
.popup__container__input-container {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  margin-bottom: 20px;
}
.popup__container__input-container input {
  font-size: 13px;
}
.popup__container__input-container label {
  color: currentColor;
  font-size: 13px;
  font-weight: 500;
}
.popup__container__input-container label span {
  margin-left: 5px;
  color: #a61d24;
}
.popup__container .btn {
  padding: 10px 25px;
  border: none;
  font-weight: 600;
  border-radius: 4px;
  margin-top: 20px;
  margin-left: auto;
  text-transform: capitalize;
  float: right;
  cursor: pointer;
  background-color: #1db954;
  background-color: var(--spotify-faded-color);
}
.popup__hover-box {
  background-color: rgba(0, 0, 0, 0.548);
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
}

@media screen and (max-width: 800px) {
  .App .sidebar__close-box {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: calc(100 * var(--vh));
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 4;
    display: inline;
    display: initial;
  }
}
@media screen and (max-width: 600px) {
  .popup__container {
    padding: 30px;
    width: 90%;
    min-width: 200px;
  }
  .popup__container h1 {
    font-size: 20px;
  }
